#textLength{
      display:inline;
    }
    #Textarea{

      resize: none;
    }
    .btn{
      border: none;
      box-shadow:6px 6px 6px rgba(0, 0, 0, 0.233);
    }
    .btn:hover{
      background-color: #131518 !important;
    }
    #clearBtn{
      background-color: #F79708!important;
    }
    #clearBtn:hover{
      background-color:#926BD3 !important;
    }
    .buttonshain{
      display: flex;
      justify-content:space-between;
    }

     .navbar{
      box-shadow:6px 6px 6px rgba(0, 0, 0, 0.233);
      display: flex;
      justify-content: space-around;
    }
   
    #accordionFlushExample{
      box-shadow:6px 6px 6px rgba(0, 0, 0, 0.233)!important;
    }
    
    textarea::-webkit-scrollbar {
      width: 10px;
      height: 10px;
    }
    
    /* Track */
    textarea::-webkit-scrollbar-track {
      background: #f1f1f1; 
    }
     
    /* Handle */
    ::-webkit-scrollbar-thumb {
      background: #888; 
    }
    
    /* Handle on hover */
    ::-webkit-scrollbar-thumb:hover {
      background: #555; 
    }

    .sameclass{
      overflow: hidden;
      position: fixed;
      bottom: 0;
      height: 8vh;
      width: 100%;
      box-shadow:6px -6px 6px rgba(0, 0, 0, 0.233)!important;
      text-align:center;
    }
    #colorpicker1{
      border: none;
    }
    .sameclass input{
      height: 15px;
      margin: 2px;
    }
    #about{
      margin-top:3vh ;
      margin-bottom: 8vh;
    }
    #linkAbout{
      text-decoration: none;
    }
    .cpybtn{
      opacity: 50%;
    }
    .cpybtn:hover{
      opacity: 100%;
    }
